main.archive,
.inline-archive,
main.search {
	@import "./header";

	@import "./list";

	@import "./cards";

	@import "./tax-children";

	@import "./search";

	/**
	 * On the blog posts page, page content may be displayed before the archive.
	 * This definition makes a space between that page content and the archive.
	 */
	.archive-content-follows + .row {
		margin-top: 1.5em;
	}
}
