.search-form {
	font-size: 1rem;

	form {
		margin: 0;
		padding: 0;
	}

	.search-inside_wrapper {
		display: flex;
		flex: auto;
		flex-wrap: nowrap;
		max-width: 100%;
		padding: 0.15em;
		border: 1px solid rgba(0, 0, 0, 0.15);
		background: transparent;
		transition: background 0.15s;
	}
	input[type="search"] {
		background: transparent;
		border-radius: 0;
		border: none;
		padding: 0 0 0 0.25em;
		flex-grow: 1;
		min-width: 3em;
		font-size: inherit;
		font-family: inherit;
		line-height: inherit;

		&:focus-visible {
			outline: 0;
		}
	}
	button {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0.25em 0.5em;
		line-height: 1;
		background: rgba(0, 0, 0, 0.15);
		border: 1px solid rgba(0, 0, 0, 0.15);
		color: #32373c;
		margin-left: 0.5em;
		word-break: normal;
		font-size: inherit;
		font-family: inherit;
		cursor: pointer;

		@include hover {
			background: var(--#{$PREFIX}-color-brand, rgba(0, 0, 0, 0.5));
			color: #fff;
		}
	}
	svg {
		min-width: 1.5em;
		min-height: 1.5em;
		fill: currentColor;
	}
}
